@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.home_page{
    display: flex;
    width: 100vw;
    height: 583px;
    justify-content: center;
    align-items: center;
    overflow: inherit;
    margin-top: 30px;
}
.login_form{
    width: 350px !important;
    height: 581.12px;
    margin: 0 2.5vw !important;
}
.home_img{
    width: 380px;
    height: 100%;
}
.up_left{
    width: 380px;
    height: 581.12px;
}
.login_form_in{
    width: 100%;
    margin: 2.2vh 0;
    height: 379.98px !important;
    /* border: 1px solid rgb(143, 139, 139); */
    box-shadow: 0px 0px 2px 0px gray;
    border-radius: 1px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}
.insta_logo{
    margin-top: 36px;
    margin-bottom: 12px!important;
    width: 175px;
    height: 55px;
}
.inp_login{
    width:255px;
    height: 36px;
    background-color: rgba(214, 204, 204, 0.144);
    border-radius: 3px;
    margin: 3px;
    border: 1px solid rgba(167, 155, 155, 0.253);
   outline-color: rgba(167, 155, 155, 0.486);
   font-size: 73%;
   padding: 4px;
   padding-left: 10px;
   padding-right: 0px;
   box-sizing: border-box;
}
.login_button{
    width: 268px !important;
    margin: 8px 40px!important;
    border-radius: 8px !important;
    background-color: rgb(0, 162, 255) !important;
    height: 32px !important;
}
.line{
    border: 0.1px solid rgba(128, 128, 128, 0.363);
    width: 31% !important;
    height: 0px;
    display: inline-block;
}
.or{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
}
.writOr{
    width: 15%;
    color: rgba(36, 34, 34, 0.678);
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    font-size: 90%;

}
.ab3a{
    height: 266.984px;
}
.facebook_link{
    width: 100%;
    display: flex;
    align-items: center !important;
    justify-content: center;
    text-decoration: none;
    font-size: 100%;
    font-style: normal;
    font-weight: bold;
    margin: 15px 0;
  
}
.forgotpass{
    margin: 15px 0;
    font-size: 70%;
    font-weight: 500;
}
.sign_up_link{
    width: 100%;
    height: 63px;
    /* border: 1px solid rgb(143, 139, 139); */
    box-shadow: 0px 0px 2px 0px gray;
    border-radius: 1px;
    margin: 2vh 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 80%;
}
.signup_link{
    text-decoration: none;
    color: rgb(0, 162, 255);
    font-weight: bold;
}
.left_img{
    position: relative;
    top: 0px;
    /* left: -14vw; */
   
    height: 581.12px;
    width: 380px;
}
.ani_img{
    position: absolute;
    left: 0px;
}
.ani_img2{
   top: 45px;
   left: 111px;
   border-radius: 20px;
   width: 242px;
   height: 500px;
}
#first_img{
    animation: home_page 18s infinite;
    z-index: 3;
}
#secnd_img{
    animation: home_page1 18s  infinite;
    animation-delay: 5.94s;
    z-index: 2;
   

}
#thrd_img{
    animation: home_page2 18s  infinite;
    animation-delay: 11.88s;
    z-index: 1;
   
   
}
@keyframes home_page{
0%{
    opacity: 100%;
}
33%{
    opacity: 0%;
}
66%{
    opacity: 0%;

}
100%{
    opacity: 100%;
}
}
@keyframes home_page1{
0%{
    opacity: 100%;
}
33%{
    opacity: 0%;
}
66%{
    opacity: 0%;

}
100%{
    opacity: 100%;
}
}
@keyframes home_page2{
0%{
    opacity: 100%;
}
33%{
    opacity: 0%;
}
66%{
    opacity: 0%;

}

100%{
    opacity: 100%;
}
}


@media screen and (max-width:875px){
    .up_left{
        display: none;
    }
   .login_form{
    width: 350px;
    height: 580px;
    
}
.login_form_in{
     margin: 0px;
    height: 379.9px;
   }
   .home_page{
    display: inline-block;
   width: 350px;
   margin: auto;
   }
   .sign_up_link{
    margin: 2vh 0;
   }
}

@media screen and (max-width:450px){
    .login_form{
        width: 100vw !important;
    }
    .login_form_in{
        box-shadow: none;
        width: 100vw;
    }
    .sign_up_link{
        box-shadow: none;
        width: 100vw;
        
    }
    .home_page{
        display: flex;
        justify-content: center;

        width: 100vw;
    }

}
@media only screen and (min-height:821px ) and (max-height:1000px){
    
    .insta_logo{
        margin-bottom: 30px !important;
    } 
    .inp_login{
        margin: 6px;
    }
    .login_button{
        margin: 15px !important;
    }
    .or{
        margin: 10px !important;
    }
    .sign_up_link{
        margin: 50px 0;
        margin-bottom: 0;
    }
    .forgotpass{
        margin: 25px 0;
        margin-bottom: 0 !important;
    }
    .facebook_link{
        margin: 25px 0;
    }
    .login_form_in{
        height: 400px !important;
    }
}
@media screen and (min-height:1000px) {
    .home_page{
        height: 100vh;
        display: flex;
    }
}
@media only screen and (min-width:1300px){
    
}
@media only screen and (min-height:600px) and (max-height:800px) {
    .home_page{
        margin-top: 32px !important;
        height: 590px !important;
    }    
}