@import url('https://fonts.googleapis.com/css2?family=Alata&display=swap');

.facebook_navbar{
    width: 100%;
    height: 10vh;
    background-color: #3b5998;
    background-image: linear-gradient(#4e69a2, #3b5998 50%);
    text-align: start;
    
}
.facebook_navbar h1{
    margin: 0px;
}
.face_link{
    color: white;
    text-decoration: none;
    font-family: 'Alata', sans-serif;
    font-size: calc((3vw + 6vh)/2);
    margin: 0 15vw;
}
.facebook_page{
    background-color: #97a2bd1f;
    width: 100%;
    height: 80vh;
}
.facebook_inp{
    width: 45vw;
    height: 50vh;
    background-color: white;
    border: 1px solid white;
    margin: 10vh auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}
.insta_logo_head{
    width: calc((1.5vw + 4vh)/2);
    height: calc((1.5vw + 4vh)/2);
}
.head_facebook{
    font-size: calc((2vw + 2vh)/2);
    display: flex;
    align-items: center;
    font-style: normal;
    margin: calc((3vw + 7vh)/2);

}
#femail_no ,#fpass{
    width: 50%;
}
.facebook_login_button{
    width: 50% !important;
}

@media screen and (max-width: 600px){
    .facebook_inp{
        width: 100vw;
    }
}
@media screen and (max-height: 1100px) and (max-width:970px){
    .head_facebook{
        flex-direction: column;
    }
    .insta_logo_head{
        margin: calc((0.7vw + 1vh)/2);
    }
}